<template>
    <div class="left">
        <ul class="left-ul">
            <li class="left-li-title">开发指南</li>
            <li class="left-li" :class="{active:menuIndex === 1}" @click="menuCk(1)">介绍</li>
            <li class="left-li" :class="{active:menuIndex === 2}" @click="menuCk(2)">快速上手</li>
            <li class="left-li" :class="{active:menuIndex === 3}" @click="menuCk(3)">编码规范</li>
            <li class="left-li" :class="{active:menuIndex === 6}" @click="menuCk(6)">常用代码片段</li>
            <li class="left-li-title">基础组件</li>
            <li class="left-li" :class="{active:menuIndex === 5}" @click="menuCk(5)">图标库</li>
            <li class="left-li" :class="{active:menuIndex === 4}" @click="menuCk(4)">按钮</li>
            <li class="left-li" :class="{active:menuIndex === 7}" @click="menuCk(7)">文字链接</li>
            <li class="left-li" :class="{active:menuIndex === 16}" @click="menuCk(16)">标记</li>
            <!--<li class="left-li" :class="{active:menuIndex === 17}" @click="menuCk(17)">面包屑</li>-->

            <li class="left-li-title">表单组件</li>
            <li class="left-li" :class="{active:menuIndex === 8}" @click="menuCk(8)">单选框（开发中）</li>
            <li class="left-li" :class="{active:menuIndex === 9}" @click="menuCk(9)">多选框（开发中）</li>
            <li class="left-li" :class="{active:menuIndex === 10}" @click="menuCk(10)">输入框（开发中）</li>
            <li class="left-li" :class="{active:menuIndex === 11}" @click="menuCk(11)">计数器（开发中）</li>
            <li class="left-li" :class="{active:menuIndex === 12}" @click="menuCk(12)">表格（开发中）</li>

            <li class="left-li-title">业务组件</li>
            <li class="left-li" :class="{active:menuIndex === 18}" @click="menuCk(18)">支付及充值</li>
            <li class="left-li" :class="{active:menuIndex === 13}" @click="menuCk(13)">地址列表</li>
            <li class="left-li" :class="{active:menuIndex === 19}" @click="menuCk(19)">地址编辑</li>
            <li class="left-li" :class="{active:menuIndex === 20}" @click="menuCk(20)">地址选择</li>
            <li class="left-li" v-show="false" :class="{active:menuIndex === 14}" @click="menuCk(14)">商品卡片</li>
            <li class="left-li" v-show="false" :class="{active:menuIndex === 15}" @click="menuCk(15)">优惠券</li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: 'Left',
        watch: {
            $route(to, from) {
                //console.log(from.path);//从哪来
                //console.log(to.path);//到哪去
                this.highMenu();
                this.setScrollTop();
            }
        },
        data() {
            return {
                menuIndex: 1
            }
        },
        mounted() {
            this.highMenu();
        },
        methods: {
            //菜单点击
            menuCk(n) {
                let currUrl = this.$route.name;
                if (n === 1) {
                    if (currUrl === 'index') {
                        return;
                    }
                    this.$router.push({
                        name: 'index'
                    });
                } else if (n === 2) {
                    if (currUrl === 'quickStart') {
                        return;
                    }
                    this.$router.push({
                        name: 'quickStart'
                    });
                } else if (n === 3) {
                    if (currUrl === 'codeStandard') {
                        return;
                    }
                    this.$router.push({
                        name: 'codeStandard'
                    });
                } else if (n === 4) {
                    if (currUrl === 'button') {
                        return;
                    }
                    this.$router.push({
                        name: 'button'
                    });
                } else if (n === 5) {
                    if (currUrl === 'icon') {
                        return;
                    }
                    this.$router.push({
                        name: 'icon'
                    });
                } else if (n === 6) {
                    if (currUrl === 'commonCode') {
                        return;
                    }
                    this.$router.push({
                        name: 'commonCode'
                    });
                } else if (n === 7) {
                    if (currUrl === 'txtLinkHref') {
                        return;
                    }
                    this.$router.push({
                        name: 'txtLinkHref'
                    });
                } else if (n === 8) {
                    if (currUrl === 'radio') {
                        return;
                    }
                    this.$router.push({
                        name: 'radio'
                    });
                } else if (n === 9) {
                    if (currUrl === 'checkbox') {
                        return;
                    }
                    this.$router.push({
                        name: 'checkbox'
                    });
                } else if (n === 10) {
                    if (currUrl === 'input') {
                        return;
                    }
                    this.$router.push({
                        name: 'input'
                    });
                } else if (n === 11) {
                    if (currUrl === 'countNumber') {
                        return;
                    }
                    this.$router.push({
                        name: 'countNumber'
                    });
                } else if (n === 12) {
                    if (currUrl === 'table') {
                        return;
                    }
                    this.$router.push({
                        name: 'table'
                    });
                }else if (n === 16) {
                    if (currUrl === 'badge') {
                        return;
                    }
                    this.$router.push({
                        name: 'badge'
                    });
                }else if (n === 17) {
                    /*if (currUrl === 'breadcrumb') {
                        return;
                    }
                    this.$router.push({
                        name: 'breadcrumb'
                    });*/
                }else if (n === 13) {
                    if (currUrl === 'addressList') {
                        return;
                    }
                    this.$router.push({
                        name: 'addressList'
                    });
                }else if (n === 14) {
                    if (currUrl === 'goodsCard') {
                        return;
                    }
                    this.$router.push({
                        name: 'goodsCard'
                    });
                }else if (n === 15) {
                    if (currUrl === 'coupon') {
                        return;
                    }
                    this.$router.push({
                        name: 'coupon'
                    });
                }else if (n === 18) {
                    if (currUrl === 'payOrRecharge') {
                        return;
                    }
                    this.$router.push({
                        name: 'payOrRecharge'
                    });
                }else if (n === 19) {
                    if (currUrl === 'addressEdit') {
                        return;
                    }
                    this.$router.push({
                        name: 'addressEdit'
                    });
                }else if (n === 20) {
                    if (currUrl === 'addressSelect') {
                        return;
                    }
                    this.$router.push({
                        name: 'addressSelect'
                    });
                }
            },
            //根据不同路由，高亮菜单
            highMenu() {
                let currUrl = this.$route.name;
                if (currUrl === 'index') {
                    this.menuIndex = 1;
                } else if (currUrl === 'quickStart') {
                    this.menuIndex = 2;
                } else if (currUrl === 'codeStandard') {
                    this.menuIndex = 3;
                } else if (currUrl === 'button') {
                    this.menuIndex = 4;
                } else if (currUrl === 'icon') {
                    this.menuIndex = 5;
                } else if (currUrl === 'commonCode') {
                    this.menuIndex = 6;
                } else if (currUrl === 'txtLinkHref') {
                    this.menuIndex = 7;
                } else if (currUrl === 'radio') {
                    this.menuIndex = 8;
                } else if (currUrl === 'checkbox') {
                    this.menuIndex = 9;
                } else if (currUrl === 'input') {
                    this.menuIndex = 10;
                } else if (currUrl === 'countNumber') {
                    this.menuIndex = 11;
                } else if (currUrl === 'table') {
                    this.menuIndex = 12;
                } else if (currUrl === 'badge') {
                    this.menuIndex = 16;
                } else if (currUrl === 'breadcrumb') {
                    this.menuIndex = 17;
                } else if (currUrl === 'addressList') {
                    this.menuIndex = 13;
                } else if (currUrl === 'goodsCard') {
                    this.menuIndex = 14;
                } else if (currUrl === 'coupon') {
                    this.menuIndex = 15;
                } else if (currUrl === 'payOrRecharge') {
                    this.menuIndex = 18;
                } else if (currUrl === 'addressEdit') {
                    this.menuIndex = 19;
                } else if (currUrl === 'addressSelect') {
                    this.menuIndex = 20;
                }
            },
            //设置滚动条高度0
            setScrollTop() {
                $('.main').scrollTop(0);
            }
        }
    }
</script>
<style scoped>
    .left {
        width: 100%;
        overflow: auto;
    }

    .left-ul {
        display: block;
        overflow: hidden;
        padding-top: 30px;
    }

    .left-li-title {
        height: 44px;
        line-height: 44px;
        display: block;
        overflow: hidden;
        color: #455a64;
        font-size: 14px;
        font-weight: bold;
        text-indent: 30px;
    }

    .left-li {
        height: 44px;
        line-height: 44px;
        display: block;
        overflow: hidden;
        color: #455a64;
        font-size: 14px;
        text-indent: 30px;
        cursor: pointer;
        -moz-user-select:none;
        -webkit-user-select:none;
        user-select:none;
    }

    .left-li.active {
        color: #4fc08d;
    }
</style>



